import 'rxjs/add/operator/toPromise';
import 'rxjs/add/operator/share'

import { Injectable } from '@angular/core';

import { Api } from '../api/api';
import {DbService} from "../db-service";



/**
 * Most apps have the concept of a User. This is a simple provider
 * with stubs for login/signup/etc.
 *
 * This User provider makes calls to our API at the `login` and `signup` endpoints.
 *
 * By default, it expects `login` and `signup` to return a JSON object of the shape:
 *
 * ```json
 * {
 *   status: 'success',
 *   user: {
 *     // User fields your app needs, like "id", "name", "email", etc.
 *   }
 * }Ø
 * ```
 *
 * If the `status` field is not `success`, then an error is detected and returned.
 */
@Injectable()
export class User {
  _user: {id:number, name:string, email:string, paid:string, picture:string};
  _dbService: DbService;

  constructor(public api: Api, dbService:DbService) {
    this._dbService = dbService;
  }

  /**
   * Send a POST request to our login endpoint with the data
   * the user entered on the form.
   */
  login(accountInfo: any) {
    let seq = this.api.post('login', accountInfo).share();

    seq.subscribe((res: any) => {
      // If the API returned a successful response, mark the user as logged in
      if (res.status == 'success') {
        console.log(res);
        this._loggedIn(res);
      } else {
        console.log(res);
      }
    }, err => {
      console.error('ERROR', err);
    });

    return seq;
  }

  /**
   * Send a POST request to our signup endpoint with the data
   * the user entered on the form.
   */
  signup(accountInfo: any) {
    let seq = this.api.post('signup', accountInfo).share();

    seq.subscribe((res: any) => {
      // If the API returned a successful response, mark the user as logged in
      if (res.status == 'success') {
        console.log(res);
        this._loggedIn(res);
      }else{
        console.log(res);
      }
    }, err => {
      console.error('ERROR', err);
    });

    return seq;
  }

  /**
   * Log the user out, which forgets the session
   */
  logout() {
    this._user = null;
  }

  /**
   * Process a login/signup response to store user data
   */
  _loggedIn(resp) {
    this._user = resp.user;
    this._dbService.init();
    this._dbService.setProperty('id',this._user.id);
    this._dbService.setProperty('userName',this._user.name);
    this._dbService.setProperty('email',this._user.email);
    this._dbService.setProperty('login', 'true');
    if(this._user.paid === "No"){
      this._dbService.setProperty('fullversion',false);
      this._dbService.setFullVersion();
    }else{
      this._dbService.setProperty('fullversion',true);
    }
    this._dbService.setProperty('picture',this._user.picture);
  }
}



